import {Component} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {ROUTER_DIRECTIVES} from "@angular/router";
import {ComponentInstruction, CanActivate} from "@angular/router-deprecated";

import {CompanyService} from "../../../lib/services/companyService";
import {isLoggedIn} from "../../../lib/const/authCheck";
import {Application} from "../../../lib/classes/application";
import {ContentService} from "../../../lib/services/contentService";
import {PeopleSearchPipe} from "../../../pipes/peopleSearchPipe";
import {Message} from "../../../lib/classes/message";
import {RoomService} from "../../../lib/services/roomService";

@Component({
    selector: 'people',
    template: require('pages/dash/people/people.component.html'),
    pipes: [PeopleSearchPipe],
    directives: [ROUTER_DIRECTIVES],
    providers: [
        HTTP_PROVIDERS,
        CompanyService,
        RoomService
    ],
    styles: [require('css/people.component.css'), require('css/app.component.css')]
})

@CanActivate((next: ComponentInstruction, previous: ComponentInstruction) => {
  return isLoggedIn(next, previous);
})

export class PeopleComponent {
    // Global fields
    public searchTerm: string;
    public allSelected: boolean;
    public applications: Application[] = [];
    public filteredApplicants: Application[] = [];
    public allApplications: Application[] = [];
    public cs: ContentService;
    public show: {};
    public message: Message = <Message>{};

    constructor(
        private _companyService: CompanyService,
        private _roomService:RoomService
    ) {
        this.searchTerm = '';
        this.allSelected = false;

        this.cs = new ContentService();

        let company_id = localStorage.getItem('comp');

        this.show = {
            statusChoises: false,
            bulkMessage: false
        }

        _companyService.getApplications(company_id)
            .subscribe(res => {
                this.applications = res;
                this.filteredApplicants = res;
                this.allApplications = res;
            });
    };

    public toggleAll(value) {
        for (var application of this.filteredApplicants) {
            application.isSelected = !this.allSelected;
        }
    }

    public changeMultipleStatus(status) {
        for (var application of this.filteredApplicants) {
            if (application.isSelected) {
                application.status = status;
                application.isSelected = false;
            }
        }
    }

    public sendBulkMessage(messageTekst) {

        let msg = {
            text: messageTekst,
            company: localStorage.getItem('comp')
        };

        var idList = [];

        for (var application of this.filteredApplicants) {
            if (application.isSelected) {
                idList.push(application.id);
            }
        }

        this._roomService.sendBulkMessage(msg, idList)
            .subscribe(res => {
                this.toggle('bulkMessage');
            });
    }

    public setSubList(status: number) {

        this.toggle('statusChoises');

        if (!status) {
            this.applications = this.allApplications;
            return;
        }

        this.applications = [];
        this.allApplications.forEach(application => {
            if (status === application.status) {
                this.applications.push(application);
            }
        });

    }

    public toggle(key) {
        this.show[key] = !this.show[key];
    }
}



