import {Component, ViewChild, ElementRef} from '@angular/core';
import {HTTP_PROVIDERS}                   from '@angular/http';
import {Router}                           from '@angular/router';

import {UserService} from "lib/services/userService";
import {Application} from "lib/classes/application";
import {RoomService} from "lib/services/roomService";
import {Room} from "lib/classes/room";
import {Message} from "lib/classes/message";
import myGlobals = require('globals');

@Component({
  selector: 'applications',
  template: require('pages/front/applications/applications.component.html'),
  directives: [],
  providers: [
    HTTP_PROVIDERS,
    UserService,
    RoomService
  ],
  styles: [require('css/applications.component.css'), require('css/app.component.css')]
})

export class ApplicationsComponent {

  /*
   *  ==================================
   *    PUBLIC FIELDS
   *  ==================================
   */

  // Application
  public applications: Application[] = [];
  public selectedApplication: Application;

  // Show/hide properties
  public show: {};

  // Chat Room
  public chatRoom: Room;
  public newMessage: string;

  // Other
  public baseUrl: string;


  /*
   *  ==================================
   *    PRIVATE FIELDS
   *  ==================================
   */

  // View childs
  @ViewChild('chatScroll') private myScrollContainer:ElementRef;

  constructor(
    private _router:Router,
    private _userService:UserService,
    private _roomService:RoomService) {

    // Retrieves applications based on logged in user
    this._userService.getUserApplications()
      .subscribe(applications => {
        this.applications = applications;
      });

    // This dict keeps track of all show/hide properties
    this.show = {
      dialog:false
    }

    // Gets the base url from globals file
    this.baseUrl = myGlobals.baseURL;
  }

  /*
  *  ==================================
  *    Public functions
  *  ==================================
  */

  // Application
  public checkStatus(status, check) {
    return status == check
  }

  public selectApplication(application) {
    this.selectedApplication = application;
    this.toggle('dialog');
    this.getChatRoom();
  }

  // Show
  public toggle(key) {
    this.show[key] = !this.show[key];
  }

  // Chat
  public getChatRoom() {
    this._roomService.getRoom(this.selectedApplication.id)
      .subscribe(room => {
        this.chatRoom = room;

        this._roomService.getMessages(room.id)
          .subscribe(messages => {
            this.chatRoom.messages = messages;
          });
      });
  }

  public postMessage(newMessage) {

    if (newMessage.trim() == '') {
      this.newMessage = "";
      return;
    }

    let msg = new Message(newMessage, this.chatRoom.id, null, true);

    this.chatRoom.messages.push(msg);
    this.newMessage = "";

    let pos = this.chatRoom.messages.length - 1;

    if (newMessage.length) {
      this._roomService.postMessage(msg)
        .subscribe(res => {
          this.chatRoom.messages[pos].is_waiting = false;
          this.chatRoom.latest_message = res;
          this.scrollToBottom(true);
        });
    }
  }

  public checkPress(event, text) {

    // Checks if the key code is equal of that of enter
    if (event.keyCode == 13 && !event.shiftKey) {

      // Calls general post method
      this.postMessage(text);


      //Prevents line shift in text area
      event.preventDefault();
    }
  }

  scrollToBottom(forceScroll:boolean):void {

    try {
      var el = this.myScrollContainer.nativeElement;

      if (forceScroll || el.scrollTop >= (el.scrollHeight - el.offsetHeight - 100)) {
        el.scrollTop = el.scrollHeight;
      }
    } catch (err) {
    }
  }
}



