import {Component} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {Router}            from '@angular/router';
import {FormBuilder, Validators} from "@angular/common";

import {User}              from '../../lib/classes/user';
import {UserService}       from '../../lib/services/userService';
import {ValidationService} from "../../lib/services/validationService";
import {mapToIterablePipe} from "../../pipes/mapToIterable";
import {PopupAlert} from "../../components/popupalert/popupalert.component";

@Component({
  selector: 'login',
  template: require('pages/login/login.component.html'),
  directives: [PopupAlert],
  providers: [
    HTTP_PROVIDERS,
    UserService
  ],
  pipes: [mapToIterablePipe],
  styles: [require('css/login.component.css')]
})

export class LoginComponent {
  public user:User = <User>{};
  public serverErrors:{};
  public loginForm:any;
  public show:{};

  constructor(private _userService:UserService,
              private _router:Router,
              private _formBuilder:FormBuilder) {
    this.loginForm = this._formBuilder.group({
      'username': ['', Validators.compose([Validators.required, ValidationService.emailValidator])],
      'password': ['', Validators.required]
    });
    this.serverErrors = {};
    this.show = {
      login: true,
      forgot: false,
      alert: false
  }
  };

  // Global functions
  login() {
    this._userService.login(this.user.username, this.user.password)
      .subscribe(res => {

        this._userService.getUserEmployments()
          .subscribe(res => {

            if (res.length) {
              this._router.navigate(['dashboard']);
            } else {
              this._router.navigate(['front', 'applications']);
            }
          }, error => {
            this.serverErrors = error;
          });
      }, error => {
        // Revieles the error to the user
        this.serverErrors = error.json();
      });
  }

  public sendMail() {
    this._userService.sendResetPassword(this.user.username)
      .subscribe(res => {
        this.alert();
      });
  }

  // Alert
  public alert() {
    this.toggle('alert');
    setTimeout(() => {
      this.toggle('alert');
    }, 2000)
  }

  public toggle(key) {
    this.show[key] = !this.show[key];
  }
}



