import 'rxjs/Rx';
import {Observable} from "rxjs/Rx";

import {Injectable}     from '@angular/core';
import {Headers} from '@angular/http';
import {AuthHttp} from 'angular2-jwt/angular2-jwt';
import {Notefication} from "../classes/notefication";
import {DashComponent} from "pages/dash/dash.component";
import {RequestService} from "./requestService";
import {RoomService} from "./roomService";

import myGlobals = require('globals');
import stream = require('getstream');

@Injectable()
export class NoteficationService {

  private _apiEndpoint = myGlobals.apiUrl + 'notifications/';  // URL to web api

  constructor(private http:AuthHttp,
              private _requestService:RequestService,
              private _roomService:RoomService) {
  }

  // Uses websockets to connect to getStrean.io API
  public connect(component:DashComponent) {

    // Retrieves a token from the server
    this.getToken()
      .subscribe(token => {

        // If a token is retrieved start the connection
        var client = stream.connect('2d2jtdtxz8zc', null, '10830');
        var companyFeed = client.feed('company', localStorage.getItem('comp'), token);

        // Subscribe to new notfifications
        var sub = companyFeed.subscribe(res => {

          /*
          *     We create a subscription to the feed of new notifications
          *     so that we know when something is updated on server
          */

          // For all new notifications we need to check if it is nessasary to invalidate any cached data
          res.new.forEach(activity => {
            if (activity.verb == 'application')
              this._requestService.invalidate('applications:' + activity.group);
            else
              this._roomService.doUpdate = true;
          });

          component.getNotefications();
        });
      });
  }

  public get() {
    return this.http.get(this._apiEndpoint + '?company=' + localStorage.getItem('comp'))
      .map(res => <Notefication> res.json());
  }

  public getToken() {
    return this.http.get(this._apiEndpoint + 'token/' + localStorage.getItem('comp') + '/')
      .map(res => res.json());
  }

  public markSeen(activity_ids?:number[]) {

    let myHeader = new Headers();
    myHeader.append('Content-Type', 'application/json');

    var body;

    console.log(activity_ids);

    if (activity_ids) {
      body = JSON.stringify({'ids':activity_ids, 'feed': 'company', 'company': localStorage.getItem('comp')});
    } else {
      body = JSON.stringify({'feed': 'company', 'company': localStorage.getItem('comp')});
    }

    return this.http.post(this._apiEndpoint + 'mark-seen/', body, {headers: myHeader});
  }

  public markRead(id) {
    let myHeader = new Headers();
    myHeader.append('Content-Type', 'application/json');
    let body = JSON.stringify({'ids': [id], 'feed': 'company', 'company': localStorage.getItem('comp')});

    return this.http.post(this._apiEndpoint + 'mark-read/', body, {headers: myHeader});
  }
}



