import {User} from "./user";
import {Note} from "./note";
import {Answer} from "./answer";
import {Job} from "./job";

export class Application {
    id: number;
    job: any;
    applicant: any;
    cover_letter: string;
    status: number;
    notes: Note[];
    answers: Answer[];
    isSelected: boolean;

    constructor(
        job: any,
        applicant: number,
        cover_letter: string,
        answers: Answer[]
    ){
      this.job = job;
      this.applicant = applicant;
      this.cover_letter = cover_letter;
      this.answers = answers;
    }
}



