import {Component, OnInit} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {ComponentInstruction, CanActivate} from "@angular/router-deprecated";

import {SearchPipe} from "../../../pipes/search.pipe";
import {CompanyService} from "../../../lib/services/companyService";
import {isLoggedIn} from "../../../lib/const/authCheck";
import {Employment} from "../../../lib/classes/employment";
import {PopupAlert} from "../../../components/popupalert/popupalert.component";

import myGlobals = require('globals');

@Component({
  selector: 'team',
  template: require('pages/dash/team/team.component.html'),
  directives: [PopupAlert],
  pipes: [SearchPipe],
  providers: [
    HTTP_PROVIDERS,
    CompanyService
  ],
  styles: [require('css/team.component.css'), require('css/app.component.css')]
})

@CanActivate((next:ComponentInstruction, previous:ComponentInstruction) => {
  return isLoggedIn(next, previous);
})

export class TeamComponent implements OnInit {

  public searchTerm:string;
  public inviteEmail:string;
  public employments:Employment[] = [];
  public show:{};
  public isLoading:boolean;
  public baseUrl:string;

  constructor(private _companyService:CompanyService) {
    this.searchTerm = '';
    this.show = {
      dialog: false,
      alert: false
    };
    this.isLoading = false;
    this.baseUrl = myGlobals.baseURL;
  }

  public ngOnInit() {
    let id = localStorage.getItem('comp');
    this._companyService.getEmployment(id)
      .subscribe(res => {
        this.employments = res;
      });
  }

  public sendInvite(email) {
    this.isLoading = true;
    this._companyService.sendInviteMail(email)
      .subscribe(res => {
        this.isLoading = false;
        this.toggle('dialog');
        this.alert();
      });
  }

  // Alert
  public alert() {
    this.toggle('alert');
    setTimeout(() => {
      this.toggle('alert');
    }, 2000)
  }

  public toggle(key) {
    this.show[key] = !this.show[key];
  }
}



