import {Component, OnInit} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {Router} from "@angular/router";
import {RouteParams} from "@angular/router-deprecated";
import {FormBuilder, Validators} from "@angular/common";

import {User} from "../../lib/classes/user";
import {Company} from "../../lib/classes/company";
import {UserService} from "../../lib/services/userService";
import {CompanyService} from "../../lib/services/companyService";
import {mapToIterablePipe} from "../../pipes/mapToIterable";
import {ValidationService} from "../../lib/services/validationService";

@Component({
    selector: 'home',
    templateUrl: 'static/pages/invite/invite.component.html',
    directives: [],
    providers: [
        HTTP_PROVIDERS,
        UserService,
        CompanyService
    ],
    pipes: [mapToIterablePipe],
    styleUrls: ['../static/css/invite.component.css', '../static/css/app.component.css']
})
export class InviteComponent implements OnInit {
    public newUser: User = <User>{};
    public company: Company = <Company>{};
    public serverErrors: {};
    public registerForm:any;

    constructor(private _userService: UserService,
                private _companyService: CompanyService,
                private _routeParams:RouteParams,
                private _formBuilder:FormBuilder,
                private _router: Router){
        this.serverErrors = {};
        this.registerForm = this._formBuilder.group({
            'first_name':['', Validators.required],
            'last_name':['', Validators.required],
            'username':['', Validators.compose([Validators.required, ValidationService.emailValidator])],
            'password':['', Validators.compose([Validators.required, ValidationService.passwordValidator])],
            'passwordCheck':['', Validators.required],
        }, {validator: ValidationService.matchingPasswords('password', 'passwordCheck')});
    }

    public ngOnInit() {
        let id = Number(this._routeParams.get('id'));
        this._companyService.getCompany(id)
            .subscribe(res => {
                this.company = res;
            });
    }

    // Function used to register company
    public registerEmployer() {
        let company_id = this.company.id;
        this._companyService.createEmployment(company_id, 'Ansatt', this.newUser)
            .subscribe(res => {

                // If the server returns errors
                if (res.errors) {
                    this.serverErrors = res.errors;
                    return;
                }

                // Navigates the user to the complete page
                this._router.navigate(['Complete', {email: this.newUser.username}]);
            });
    }
}



