import {User} from "./user";
import {ServerResponse} from "./serverResponse";

export class Message extends ServerResponse {
  id:number;
  text:string;
  author:number;
  room:number;
  company:number;
  is_waiting:boolean;
  is_read:boolean;

  constructor(text:string, room:number, company:number, is_waiting:boolean) {
    super();
    this.text = text;
    this.room = room;
    this.company = company;
    this.is_waiting = is_waiting;
  }
}



