import {Component} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {CanActivate} from "@angular/router-deprecated";
import {ComponentInstruction} from "@angular/router-deprecated";
import {NgZone} from "@angular/core";
import {UPLOAD_DIRECTIVES} from 'ng2-uploader/ng2-uploader';

import {PopupAlert} from "../../../components/popupalert/popupalert.component";
import {UserService} from "../../../lib/services/userService";
import {isLoggedIn} from "../../../lib/const/authCheck";
import {User} from "../../../lib/classes/user";
import myGlobals = require('globals');

@Component({
  selector: 'profile',
  template: require('pages/dash/profile/profile.component.html'),
  directives: [UPLOAD_DIRECTIVES, PopupAlert],
  providers: [
    HTTP_PROVIDERS,
    UserService
  ],
  styles: [require('css/profile.component.css'), require('css/app.component.css')]
})

@CanActivate((next:ComponentInstruction, previous:ComponentInstruction) => {
  return isLoggedIn(next, previous);
})

export class ProfileComponent {
  public user:User = <User>{};
  public show:{};
  public isLoading:boolean;
  public baseUrl:string;

  // File upload
  zone:NgZone;

  options = {
    url: myGlobals.apiUrl + 'users/',
    fieldName: 'user_profile.profile_picture',
    method: 'PUT',
    withCredentials: true,
    authToken: localStorage.getItem('id_token'),
    authTokenPrefix: "JWT"
  };
  dropzone:any;
  imageProgress:number = 0;
  imageResp:any[] = [];

  constructor(private _userService:UserService) {
    this.show = {
      'dialog': false
    }

    this.zone = new NgZone({enableLongStackTrace: false});
    this.isLoading = false;

    this.baseUrl = myGlobals.baseURL;
  }

  public ngOnInit() {
    this._userService.getCurrentUser()
      .subscribe(res => {
        this.user = res;
        this.options.url = myGlobals.apiUrl + 'users/' + res.id + '/';
      });
  }

  public toggle(key) {
    this.show[key] = !this.show[key];
  }

  public updateUser() {
    this._userService.update(this.user)
      .subscribe(res => {
        this.alert();
      });
  }

  // Alert
  public alert() {
    this.toggle('alert');
    setTimeout(() => {
      this.toggle('alert');
    }, 2000)
  }

  // File Upload
  public handleDropUpload(data) {
    if (data && data.response) {
      this.isLoading = false;
      this.user = JSON.parse(data.response);
      this.toggle('dialog');
    }
  }

  public uploadStart($event) {
    this.isLoading = true;
  }
}



