import {Directive, ElementRef, Input, OnInit} from '@angular/core';

@Directive({
    selector: 'scroll-tracker',
    providers: [Window]
})
export class ScrollTracker implements OnInit{

    @Input() className: any;
    @Input('id') id: Number;

    private elHeight: number;

    constructor(private el: ElementRef) {
    }

    ngOnInit(){
        this.elHeight = this.el.nativeElement.offsetTop + 45;
        window.addEventListener('scroll', this.onScroll(this.el, this.className, this.elHeight));
        document.addEventListener('ready', function(){
            console.log("ready");
        });
    }

    onScroll (el: ElementRef, className:string, height: number) {
        return function(){
            if (window.pageYOffset > height) {
                el.nativeElement.classList.add(className);
            } else {
                el.nativeElement.classList.remove(className);
            }
        }
    }
}



