import 'rxjs/Rx';
import {Observable}     from 'rxjs/Observable';
import {Injectable}     from '@angular/core';
import {Http, Response} from '@angular/http';
import {RequestOptions, Headers} from "@angular/http";

import {AuthHttp} from 'angular2-jwt/angular2-jwt';
import {Company} from "../classes/company";
import {Employment} from "../classes/employment";
import {Room} from "../classes/room";
import {Application} from "../classes/application";
import {MessageTemplate} from "../classes/messageTemplate";
import {User} from "../classes/user";
import myGlobals = require('globals');

@Injectable()
export class CompanyService {
    constructor(private http:Http, private authHttp: AuthHttp) {
    }

    private _apiEndpoint = myGlobals.apiUrl + 'companies/';  // URL to web api

    public getCompany(id: number) {
        return this.authHttp.get(this._apiEndpoint + id + '/')
            .map(res => <Company> res.json());
    }

    public register(company: Company, user: User) {

        let myHeader = new Headers();
        myHeader.append('Content-Type', 'application/json');
        let body = JSON.stringify({company: company, user: user});

        return this.http.post(this._apiEndpoint, body, {headers: myHeader})
            .map(res => <Company> res.json());
    }

    public update(company: Company) {

        // Need to delete logo string in order to update other info
        var copy = Object.assign({}, company);
        delete copy.logo;

        let myHeader = new Headers();
        myHeader.append('Content-Type', 'application/json');
        let body = JSON.stringify(copy);

        return this.authHttp.put(this._apiEndpoint + company.id + '/', body, {headers: myHeader})
            .map(res => <Company> res.json());
    }

    public createEmployment(company_id: number, position: string, user: User) {

        user.user_profile = {};

        let myHeader = new Headers();
        myHeader.append('Content-Type', 'application/json');
        let body = JSON.stringify({employment: {company: company_id, position: position}, user: user});

        return this.authHttp.post(this._apiEndpoint + 'employment/', body, {headers: myHeader})
            .map(res => <Employment> res.json());
    }

    public getEmployment(company_id: number) {
        return this.authHttp.get(this._apiEndpoint + 'employment/' + company_id + '/')
            .map(res => <Employment[]> res.json());
    }

    public getRooms(company_id: number) {
        return this.authHttp.get(this._apiEndpoint + company_id + '/rooms/')
            .map(res => <Room[]> res.json());
    }

    public getApplications(company_id: number) {
        return this.authHttp.get(this._apiEndpoint + company_id + '/applications/')
            .map(res => <Application[]> res.json());
    }

    // Message templates assosiated with each company profile
    // ----------------------------------------------------------

    public getMessageTemplates(company_id: number) {
        return this.authHttp.get(this._apiEndpoint  + company_id + '/templates/')
            .map(res => <MessageTemplate[]> res.json());
    }

    public createMessageTemplate(template: MessageTemplate) {
        let myHeader = new Headers();
        myHeader.append('Content-Type', 'application/json');
        let body = JSON.stringify(template);

        return this.authHttp.post(this._apiEndpoint + template.company + '/templates/', body, {headers: myHeader})
            .map(res => <MessageTemplate> res.json());
    }

    public updateMessageTemplate(template: MessageTemplate) {
        let myHeader = new Headers();
        myHeader.append('Content-Type', 'application/json');
        let body = JSON.stringify(template);

        return this.authHttp.put(this._apiEndpoint + template.company + '/templates/' + template.id + '/', body, {headers: myHeader})
            .map(res => <MessageTemplate> res.json());
    }

    public sendInviteMail(email: string) {
        let myHeader = new Headers();
        myHeader.append('Content-Type', 'application/json');
        let body = JSON.stringify({email:email, company_id: localStorage.getItem('comp')});

        return this.authHttp.post(this._apiEndpoint + 'send-invite/', body, {headers: myHeader});
    }
}



