import {Component, OnInit} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {FormBuilder, Validators} from "@angular/common";
import {Router} from "@angular/router";

import {User} from "../../lib/classes/user";
import {Company} from "../../lib/classes/company";
import {UserService} from "../../lib/services/userService";
import {CompanyService} from "../../lib/services/companyService";
import {ValidationService} from "../../lib/services/validationService";

@Component({
    selector: 'register',
    template: require('pages/register/register.component.html'),
    directives: [],
    providers: [
        HTTP_PROVIDERS,
        UserService,
        CompanyService
    ],
    styles: [require('css/register.component.css')]
})
export class RegisterComponent {
    public newCompany: Company = <Company>{};
    public newUser: User = <User>{};
    public registerForm:any;
    public serverErrors: {} = {};

    constructor(
        private _userService:UserService,
        private _companyService:CompanyService,
        private _formBuilder:FormBuilder,
        private _router:Router
    ) {
        this.registerForm = this._formBuilder.group({
            'name':['', Validators.required],
            'description': ['', Validators.required],
            'first_name':['', Validators.required],
            'last_name':['', Validators.required],
            'username':['', Validators.compose([Validators.required, ValidationService.emailValidator])],
            'password':['', Validators.compose([Validators.required, ValidationService.passwordValidator])],
            'passwordCheck':['', Validators.required],
        }, {validator: ValidationService.matchingPasswords('password', 'passwordCheck')});
    }

    // Function used to register company
    public registerCompany() {

        // Server expects a value for user_profile
        this.newUser.user_profile = {};

        // Registers both user and company in one request
        this._companyService.register(this.newCompany, this.newUser)
            .subscribe(res => {
                // Navigates the user to the complete page
                this._router.navigate(['complete', this.newUser.username]);
            }, error => {
              this.serverErrors = error.json();
            });
    }
}



