import 'rxjs/Rx';
import {Injectable}     from 'angular2/core';
import {AuthHttp} from 'angular2-jwt/angular2-jwt';
import {Job} from "../classes/job";
import {Application} from "../classes/application";
import {Note} from "../classes/note";
import myGlobals = require('globals');
import {Question} from "../classes/question";
import {RequestService} from "./requestService";

@Injectable()
export class JobService {

  /*
   * ====================================
   *   JOB SERVICE
   * ====================================
   *
   * The job service handles all queries related to a job. This includes:
   *
   *    - Applications
   *    - Questions
   *    - Application Notes
   *    - Category images
   *
   */

  // URL to web api
  private _apiEndpoint = myGlobals.apiUrl;

  constructor(
    private http:AuthHttp,
    private _requestService:RequestService
  ) {}


  /*
   * ====================================
   *   JOB
   * ====================================
   */

  public getJob(job_id) {

    /*
    *
    * This method allows both anonymous and logged in users to
    * request data regarding a single job.
    *
    * Date: 24.06.16
    *
    * When the data is returned, it is parsed to json and casted as a instance of
    * the class Job
    *
    */

    // Returns an observable that maps and casts the result
    return this._requestService.request('GET', this._apiEndpoint + 'jobs/' + job_id + '/')
      .map(res => <Job> res.json());
  }

  public postJob(job:Job) {

    /*
     *
     * To post a job, the user needs to be authenticated. And after the server saves the job to
     * the database, it returns the job as a json serializable string.
     *
     * Date: 24.06.16
     *
     * When the data is returned, it is parsed to json and casted as a instance of
     * the class Job
     *
     */

    // Retrieves the company_id of the logged in user
    job.company = localStorage.getItem('comp');

    // Returns an observable that maps and casts the result
    return this._requestService.request('POST', this._apiEndpoint + 'jobs/', null, job)
      .map(res => <Job> res.json());
  }

  public updateJob(job:Job) {

    /*
     *
     * This method is used to update an already existing job and returns the
     * updated job as a serializable string.
     *
     */

    /*
     *
     * In order to change some properties of the JSON-object that is sent to server we need
     * to copy the object before we change the fields.
     *
     */

    // Copies the object
    var copy = Object.assign({}, job);
    copy.company = copy.company.id;
    delete copy.image;

    // Returns an observable that maps and casts the result
    return this._requestService.request('PUT', this._apiEndpoint + 'jobs/' + job.id + '/', null, copy)
      .map(res => <Job> res.json());
  }

  /*
   * ====================================
   *   COMPANY JOBS LIST
   * ====================================
   */

  public getCompanyJobs(companyId) {

    var key = 'companyJobs';

    return this._requestService.request('GET', this._apiEndpoint + 'companies/' + companyId + '/jobs/', key)
      .map(res => <Job[]> res.json());
  }

  /*
   * ====================================
   *   APPLICATION
   * ====================================
   */

  public getApplications(job_id) {

    var key = 'applications:' + job_id;

    return this._requestService.request('GET', this._apiEndpoint + 'jobs/' + job_id + '/applications/', key)
      .map(res => <Application[]> res.json());
  }

  public postApplication(application) {

    return this._requestService.request('POST', this._apiEndpoint + 'jobs/' + application.job + '/applications/', '', application)
      .map(res => <Application> res.json());
  }

  public updateApplication(application) {
    // Makes a copy of object so that all object references is by id
    var copy = Object.assign({}, application);
    copy.applicant = copy.applicant.id;
    copy.job = copy.job.id;

    return this._requestService.request('PUT', this._apiEndpoint + 'jobs/' + copy.job + '/applications/' + copy.id + '/', '', copy)
      .map(res => <Application> res.json());
  }

  public checkApplication(job_id) {
    return this._requestService.request('GET', this._apiEndpoint + 'jobs/' + job_id + '/applications/exists')
      .map(res => <boolean> res.json());
  }


  /*
   * ====================================
   *   QUESTIONS
   * ====================================
   */

  public getJobQuestions(job_id) {

    return this._requestService.request('GET', this._apiEndpoint + 'jobs/' + job_id + '/questions/')
      .map(res => <Question[]> res.json());
  }

  public updateQuestion(question, job_id) {

    return this._requestService.request('PUT', this._apiEndpoint + 'jobs/' + job_id + '/questions/', '',question)
      .map(res => <Question> res.json());
  }

  /*
   * ====================================
   *   APPLICATION NOTES
   * ====================================
   */

  public getApplicationNotes(application_id, job_id) {

    return this._requestService.request('GET', this._apiEndpoint + 'jobs/' + job_id + '/applications/' + application_id + '/notes/')
      .map(res => <Note[]> res.json());
  }

  public postApplicationNote(note, application_id, job_id) {

    return this._requestService.request('POST', this._apiEndpoint + 'jobs/' + job_id + '/applications/' + application_id + '/notes/', '',note)
      .map(res => <Note> res.json());
  }

  /*
   * ====================================
   *   CATEGORY IMAGES
   * ====================================
   */

  public getCategoryImages(category) {

    var key = 'catImages:' + category;

    return this._requestService.request('GET',this._apiEndpoint + 'jobs/category_image/' + category + '/', key)
      .map(res => res.json());
  }
}



