import {Component}       from '@angular/core';
import {Router, Routes, ROUTER_DIRECTIVES} from '@angular/router';
import {UserService} from "../../lib/services/userService";
import {CompanyService} from "../../lib/services/companyService";
import {DashboardComponent} from "./dashboard/dashboard.component";
import {NoteficationService} from "../../lib/services/noteficationService";
import {MessagesComponent} from "./messages/messages.component";
import {PostComponent} from "./post/post.component";
import {JobComponent} from "./job/job.component";
import {MyJobsComponent} from "./myjobs/myjobs.component";
import {PeopleComponent} from "./people/people.component";
import {TeamComponent} from "./team/team.component";
import {TemplateComponent} from "./templates/templates.component";
import {ProfileComponent} from "./profile/profile.component";
import {CompanyComponent} from "./company/company.component";
import {User} from "../../lib/classes/user";
import {Notefication} from "../../lib/classes/notefication";
import myGlobals = require('globals');
import {appInjector} from "../../lib/injectors/appInjector";
import {CanActivate} from "@angular/router-deprecated";

@Component({
  selector: 'dash',
  template: require('pages/dash/dash.component.html'),
  styles: [require('css/app.component.css'), require('css/dash.component.css')],
  directives: [ROUTER_DIRECTIVES],
  providers: [CompanyService, NoteficationService, UserService]
})

//noinspection TypeScriptValidateTypes
@Routes([
  {path: '', component: DashboardComponent},
  {path: 'messages', component: MessagesComponent},
  {path: 'post', component: PostComponent},
  {path: 'job/:id', component: JobComponent},
  {path: 'myjobs', component: MyJobsComponent},
  {path: 'people', component: PeopleComponent},
  {path: 'team', component: TeamComponent},
  {path: 'templates', component: TemplateComponent},
  {path: 'profile', component: ProfileComponent},
  {path: 'company', component: CompanyComponent}
])

export class DashComponent {
  public activeItem:string;
  public user:User;
  public show:{};
  public notifications:Notefication[];
  public notificationCount:number;
  public notificationDict:{};
  public baseUrl:string;

  constructor(private _router:Router,
              private _noteficationService:NoteficationService,
              private _userService:UserService) {
    this.show = {
      notifications: false
    };

    this.notifications = [];
    this.notificationCount = 0;

    this.baseUrl = myGlobals.baseURL;

    // Dict used to count different types of notifications
    this.notificationDict = {
      'message': [],
      'application': []
    };
  };

  ngOnInit() {
    this.activeItem = 'Post';

    this._userService.getCurrentUser()
      .subscribe(res => {
        this.user = res;
      });

    // Retrieves notifications after refresh
    this.getNotefications();

    this._noteficationService.connect(this);
  }

  public checkItem(path) {
    return this.activeItem == path;
  }

  public getNotefications() {
    this._noteficationService.get()
      .subscribe(res => {
        this.notificationCount = res.unseen;
        this.notifications = res.notifications;

        // Resets the notification count
        this.notificationDict['message'] = [];
        this.notificationDict['application'] = [];

        // Divides the notifications into different categories
        this.notifications.forEach(n => {
          if (!n.is_seen) {
            switch (n.verb) {
              case 'message':
                this.notificationDict['message'].push(n.id);
                break;
              case 'application':
                this.notificationDict['application'].push(n.id);
                break;
            }
          }
        });
      });
  }

  public openFeed() {
    this.markAsSeen();
    this.toggle('notifications');
  }

  public markAsSeen(activity_ids?:number[]) {

    if (this.notificationCount) {
      this._noteficationService.markSeen(activity_ids)
        .subscribe(res => {
          this.notificationCount = 0;

          // Resets the notification count
          if (activity_ids) {
            activity_ids = [];
          } else {
            this.notificationDict['message'] = [];
            this.notificationDict['application'] = [];
          }
        });
    }
  }

  public pressNotification(notification) {
    this.toggle('notifications');

    this._noteficationService.markRead(notification.id)
      .subscribe(res => {
        notification.is_read = true;
      });

    switch (notification.verb) {
      case 'message':
        this.navigate('messages');
        return;
      case 'application':
        this.navigate('myjobs');
        return;
    }
  }

  public makeHeader(notification) {

    var verb = notification.verb;
    var count = notification.activity_count;
    var actor = notification.activities[0].actor;

    var str = count;

    switch (verb) {
      case 'message':
        str += (count == 1) ? ' ny melding' : ' nye meldinger';
        str += ' fra ' + actor.first_name + ' ' + actor.last_name;
        return str;
      case 'application':
        str += (count == 1) ? ' ny søknad' : ' nye søknader';
        return str;
    }
  }

  public makeFooter(notification) {
    var verb = notification.verb;
    var title = notification.activities[0].job_title;
    var str = 'Jobb: ' + title;
    return str;
  }

  public navigate(path) {
    this.activeItem = path;

    switch (path) {
      case 'messages':
        this.markAsSeen(this.notificationDict['message']);
        break;
      case 'myjobs':
        this.markAsSeen(this.notificationDict['application']);
        break;
    }

    this._router.navigate(['dashboard', path]);
  }

  public toggle(key) {
    this.show[key] = !this.show[key];
  }
}



