import 'rxjs/Rx';
import {Component, OnInit}       from '@angular/core';
import {HomeComponent} from '../pages/home/home.component';
import {LoginComponent} from '../pages/login/login.component';
import {Routes, ROUTER_DIRECTIVES, Router} from '@angular/router';
import {NgClass} from "@angular/common";
import {JwtHelper} from 'angular2-jwt/angular2-jwt';

import {NoteficationService} from "./services/noteficationService";
import {DashComponent} from "../pages/dash/dash.component";
import {RegisterComponent} from "../pages/register/register.component";
import {InviteComponent} from "../pages/invite/invite.component";
import {CompleteComponent} from "../pages/complete/complete.component";
import {AuthComponent} from "../pages/auth/auth.component";
import {ResetComponent} from "../pages/reset/reset.component";
import {FrontComponent} from "../pages/front/front.component";

@Component({
  selector: 'app',
  template: `<router-outlet></router-outlet>`,
  styleUrls: ['css/app.component.css'],
  directives: [ROUTER_DIRECTIVES, NgClass],
  providers: [NoteficationService]
})

//noinspection TypeScriptValidateTypes
@Routes([
  {path: '', component: HomeComponent},
  {path: 'dashboard', component: DashComponent},
  {path: 'front', component: FrontComponent},
  {path: 'login', component: LoginComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'invite/:id', component: InviteComponent},
  {path: 'complete/:email', component: CompleteComponent},
  {path: 'auth/:token', component: AuthComponent},
  {path: 'reset-password/:token', component: ResetComponent}
])

export class AppComponent implements OnInit {

  activeItem:string;

  private _jwtHelper:JwtHelper = new JwtHelper();

  constructor(private _router:Router, private _noteficationService:NoteficationService) {

    var token = localStorage.getItem('id_token');

    if (!token || this._jwtHelper.isTokenExpired(token)) {
      this._router.navigate(['login']);
    }

  };

  ngOnInit() {
    this.activeItem = 'Post';

    this._router.changes.subscribe(val => {
      window.scrollTo(0, 0);
    });
  }

  checkItem(path) {
    return this.activeItem == path;
  }

  navigate(path) {
    this.activeItem = path
    this._router.navigate([path]);
  }
}

export function checkToken() {

  let token = localStorage.getItem('id_token');

  if (!token) {
    return false;
  }

  return true;
}



