import {Pipe} from 'angular2/core';
import {Inject} from "angular2/core";
import {forwardRef} from "angular2/core";
import {PeopleComponent} from "../pages/dash/people/people.component";

@Pipe({
  name: "search",
  pure: false
})

export class PeopleSearchPipe {

  constructor(@Inject(forwardRef(() => PeopleComponent)) private comp:PeopleComponent) {

  };

  transform(value, [term]) {
    // To lower case
    term = term.toLowerCase();

    if (value && value.length && term && term.length) {
      let val = value.filter((item) => {
        return this.search(item, [term]);
      });

      if (val.length == 0) {
        return [-1]
      }

      this.comp.filteredApplicants = val;

      return val
    }
    return value
  }

  private search(item, [term]) {
    for (var key in item) {
      if (typeof item[key] == "string" && item[key].toLowerCase().indexOf(term) != -1) {
        return true;
      } else if (typeof item[key] == "object") {
        if (this.search(item[key], [term])) {
          return true;
        }
      }
    }
    return false;
  }
}



