import {Component, OnInit, EventEmitter, ViewChild, ElementRef} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {Output} from "@angular/core";
import {ComponentInstruction} from "@angular/router-deprecated";
import {Subscription} from "rxjs/Subscription";
import {IntervalObservable} from "rxjs/observable/IntervalObservable";
import {Router, ROUTER_DIRECTIVES, RouteSegment} from "@angular/router";

import {AutoScroll} from "../../../lib/directives/autoscroll";
import {CompanyService} from "../../../lib/services/companyService";
import {RoomService} from "../../../lib/services/roomService";
import {UserService} from "../../../lib/services/userService";
import {JobService} from "lib/services/jobService";

// Custom Pipes
import {OrderByPipe} from "pipes/orderByPipe";
import {FilterStatusPipe} from "pipes/statusFilter.pipe";
import {Room} from "../../../lib/classes/room";
import {User} from "../../../lib/classes/user";
import {ContentService} from "../../../lib/services/contentService";
import {SearchPipe} from "../../../pipes/search.pipe";
import {Message} from "../../../lib/classes/message";
import myGlobals = require('globals');
import {DropdownFilter} from "../../../components/dropdownfilter/dropdownfilter.component";

@Component({
  selector: 'messages',
  template: require('pages/dash/messages/messages.component.html'),
  directives: [AutoScroll, ROUTER_DIRECTIVES, DropdownFilter],
  providers: [
    HTTP_PROVIDERS,
    CompanyService,
    UserService
  ],
  pipes: [OrderByPipe, SearchPipe, FilterStatusPipe],
  styles: [require('css/messages.component.css'), require('css/app.component.css')]
})

export class MessagesComponent implements OnInit {

  @ViewChild('chatScroll') private myScrollContainer:ElementRef;

  public allRoomsActive:Room[] = [];
  public roomsActive:Room[] = [];

  public roomsDeactive:Room[] = [];

  public selectedRoom:Room = <Room>{};
  public newMessage:string;
  public newNote:string;
  public user:User = <User>{};
  public show:any;
  public messageObservable:Subscription;
  public newMessageTerm:string;
  public baseUrl:string;
  public isLoading:boolean;

  // Filter value
  public filterValue:any;
  public jobFilterValue:any;
  public cs:ContentService;
  public jobFilter:{};

  @Output()
  public update = new EventEmitter();

  constructor(private _router:Router,
              private _routeSegment:RouteSegment,
              private _companyServie:CompanyService,
              private _roomService:RoomService,
              private _userService:UserService,
              private _jobService:JobService) {
    this.cs = new ContentService();
    this.show = {
      dialog: false,
      messageBox: true,
      noteBox: false,
      createNewMessage: false
    };

    // Starts with showing all values
    this.filterValue = 'default';
    this.jobFilterValue = 'default';

    this.jobFilter = {};

    this.cs = new ContentService();

    this.baseUrl = myGlobals.baseURL;

    this.newMessageTerm = '';

    this._router.changes.subscribe(val => {
      if (this.messageObservable) {
        this.messageObservable.unsubscribe();
      }
    });
  }

  ngOnInit() {

    let application_id = this._routeSegment.getParam('application');

    this.isLoading = true;

    this._userService.getCurrentUser()
      .subscribe(res => {
        this.user = res;
      });

    // Company id for logged in user
    let company_id = localStorage.getItem('comp');

    this._jobService.getCompanyJobs(company_id)
      .subscribe(jobs => {

        jobs.forEach(job => {
          this.jobFilter[job.id] = {text: job.title, icon: 'fa-briefcase', status: job.id}
        });
      });

    this._companyServie.getRooms(company_id)
      .subscribe(res => {
        if (res.length) {

          var is_set = false;

          res.forEach(room => {
            if (room.latest_message) {
              this.allRoomsActive.push(room);
            } else {
              this.roomsDeactive.push(room);
            }

            if (application_id) {
              if (room.application.id == Number(application_id)) {
                this.setChatRoom(room);
                is_set = true;
              }
            }
          });

          this.allRoomsActive.sort((a:any, b:any) => {
            return (a.application.status === b.application.status) ? 0 : a.application.status == 5 ? 1 : -1;
          });

          if (!is_set) {
            this.setChatRoom(this.allRoomsActive[0]);
          }

          this.roomsActive = this.allRoomsActive;

          this.messageObservable = IntervalObservable.create(5000).subscribe(res => {
            if (this.selectedRoom.id && this._roomService.doUpdate) {
              this._roomService.getMessages(this.selectedRoom.id)
                .subscribe(res => {
                  this.selectedRoom.messages = res;
                  this.selectedRoom.latest_message = res[res.length - 1];
                });
            }
          });
        }

        this.isLoading = false;

      });
  }

  routerOnDeactivate(next:ComponentInstruction, prev:ComponentInstruction) {
    if (this.messageObservable)
      this.messageObservable.unsubscribe();
  }

  // Chat Scroll
  ngAfterViewChecked() {
    this.scrollToBottom(false);
  }

  scrollToBottom(forceScroll:boolean):void {

    try {
      var el = this.myScrollContainer.nativeElement;

      if (forceScroll || el.scrollTop >= (el.scrollHeight - el.offsetHeight - 100)) {
        el.scrollTop = el.scrollHeight;
      }
    } catch (err) {
    }
  }

  public setChatRoom(room) {
    this.selectedRoom = room;

    if (room.latest_message) {
      this.selectedRoom.latest_message.is_read = true;
    }

    this._roomService.getMessages(room.id)
      .subscribe(res => {
        this.selectedRoom.messages = res;

        // Waits for DOM to update
        setTimeout(() => {
          this.scrollToBottom(true);
        }, 500);

        this._userService.getUser(this.selectedRoom.applicant.id)
          .subscribe(res => {
            this.selectedRoom.applicant = res;
          });

        this._jobService.getApplicationNotes(this.selectedRoom.application.id, this.selectedRoom.application.job)
          .subscribe(res => {
            this.selectedRoom.application.notes = res;
          });
      });
  }

  public cancelMessage() {
    this.setChatRoom(this.roomsActive[0]);
  }

  public postMessage(newMessage) {

    if (newMessage.trim() == '') {
      this.newMessage = "";
      return;
    }

    let msg = new Message(newMessage, this.selectedRoom.id, localStorage.getItem('comp'), true);

    this.selectedRoom.messages.push(msg);
    this.newMessage = "";

    let pos = this.selectedRoom.messages.length - 1;

    if (newMessage.length) {
      this._roomService.postMessage(msg)
        .subscribe(res => {
          this.selectedRoom.messages[pos].is_waiting = false;
          this.selectedRoom.latest_message = res;
          this.scrollToBottom(true);
        });
    }
  }

  public postNote(text) {

    // Used to handle different depth in serializers
    var copy = Object.assign({}, this.selectedRoom.application);

    var job_id = this.selectedRoom.application.job;
    var application_id = this.selectedRoom.application.id;

    let note = {
      text: text,
      application: application_id
    }

    this._jobService.postApplicationNote(note, application_id, job_id)
      .subscribe(res => {
        this.newNote = "";
        this.selectedRoom.application.notes.push(res);
      });
  }

  public isTyping(event, term) {


    if (!term || (term && term.length <= 1 && event.keyCode == 8)) {
      this.show.createNewMessage = false;
    } else {
      this.show.createNewMessage = true;
    }
  }

  public checkPress(event, text) {

    // Checks if the key code is equal of that of enter
    if (event.keyCode == 13 && !event.shiftKey) {

      // Calls general post method
      if (this.show.messageBox) {
        this.postMessage(text);
      } else {
        this.postNote(text);
      }

      //Prevents line shift in text area
      event.preventDefault();
    }
  }

  /*
   * ==================================
   *   FILTER DROPDOWN
   * ==================================
   */

  public setFilterValue(value, type) {

    if (type == 'status')
      this.filterValue = value;
    else
      this.jobFilterValue = value;

    this.roomsActive = this.allRoomsActive;

    if (!(this.filterValue == 'default'))
      this.roomsActive = this.roomsActive.filter((item) => {
        return item.application.status == this.filterValue;
      });

    if (!(this.jobFilterValue == 'default'))
      this.roomsActive = this.roomsActive.filter((item) => {
        return item.application.job == this.jobFilterValue;
      });

  }

  public navigate(pathList) {
    this._router.navigate(pathList);
  }

  public orderByDate(data) {
    var d = new Date(data);
    return d.getMilliseconds();
  }

  public checkOwner(id) {
    return localStorage.getItem('comp') == id;
  }

  public checkLength(list) {
    return list.length > 0;
  }

  public doShow(key:string, hide:string[]) {
    this.show[key] = true;
    hide.forEach(h => {
      this.show[h] = false;
    });
  }

  public toggle(key:string) {
    this.show[key] = !this.show[key];
  }

  public covertDate(date) {
    return new Date(date);
  }

  public setPicture(url: string) {
    if (url.indexOf('fbcdn') == -1)
      return this.baseUrl + url;

    return url;
  }
}



