import {Component, OnInit, NgZone} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {RouteSegment, ROUTER_DIRECTIVES, Router} from '@angular/router';

import {JobService} from "../../../lib/services/jobService";
import {Job} from "../../../lib/classes/job";
import {UPLOAD_DIRECTIVES} from 'ng2-uploader/ng2-uploader';
import {ScrollTracker} from "../../../lib/directives/scrollTracker";
import {PopupAlert} from "../../../components/popupalert/popupalert.component";
import {CompanyService} from "../../../lib/services/companyService";
import {RoomService} from "../../../lib/services/roomService";
import {UserService} from "../../../lib/services/userService";
import {OrderByPipe} from "../../../pipes/orderByPipe";
import {Application} from "../../../lib/classes/application";
import {MessageTemplate} from "../../../lib/classes/messageTemplate";
import {ContentService} from "../../../lib/services/contentService";
import {User} from "../../../lib/classes/user";
import {Message} from "../../../lib/classes/message";
import myGlobals = require('globals');
import {Question} from "../../../lib/classes/question";
import {QuestionAlternative} from "../../../lib/classes/questionAlternative";
import {DropdownFilter} from "../../../components/dropdownfilter/dropdownfilter.component";
import {FilterStatusPipe} from "../../../pipes/statusFilter.pipe";

@Component({
  selector: 'job',
  template: require('pages/dash/job/job.component.html'),
  directives: [ROUTER_DIRECTIVES, ScrollTracker, UPLOAD_DIRECTIVES, PopupAlert, DropdownFilter],
  providers: [
    HTTP_PROVIDERS,
    CompanyService,
    RoomService,
    UserService
  ],
  pipes: [OrderByPipe, FilterStatusPipe],
  styles: [require('css/job.component.css'), require('css/app.component.css')]
})

export class JobComponent implements OnInit {

  public job:Job = <Job>{};

  /*
  * Questions, used in this context for editorial reasons
  */
  public questions = [];
  public selectedQuestion: Question;
  public newAlternative: QuestionAlternative = <QuestionAlternative>{};

  public applications:Application[] = [];
  public selectedApplication:Application = <Application>{};
  public templates:MessageTemplate[];
  public newNote:string;
  public isUpdated:boolean;
  public show:{};
  public cs:ContentService;
  public selectedTemplate:MessageTemplate;
  public user:User;
  public dialogText:string;
  public isLoading:boolean;
  public baseUrl:string;

  // Filter value
  public filterValue;

  className = "woifhsowi";

  // File upload
  zone:NgZone;
  options = {
    url: myGlobals.apiUrl + 'jobs/',
    fieldName: 'image',
    method: 'PUT',
    withCredentials: true,
    authToken: localStorage.getItem('id_token'),
    authTokenPrefix: "JWT"
  };

  dropzone:any;
  imageProgress:number = 0;
  imageResp:any[] = [];

  constructor(
              private _router:Router,
              private _routeSegment:RouteSegment,
              private _jobService:JobService,
              private _companyService:CompanyService,
              private _roomService:RoomService,
              private _userService:UserService) {
    this.show = {
      dialog: false,
      template: false,
      mojobDialog: false,
      fileUpload: false,
      alert: false,
      requiredMessage: false,
      questions: false
    };

    this.baseUrl = myGlobals.baseURL;

    // File upload
    this.zone = new NgZone({enableLongStackTrace: false});

    this.selectedTemplate = new MessageTemplate();

    this.cs = new ContentService();

    // Sets filter value to what is defined as all
    this.filterValue = 'default';
  }

  ngOnInit() {
    let job_id = this._routeSegment.getParam('id');
    let application_id = this._routeSegment.getParam('application');

    this.isLoading = true;

    this._userService.getCurrentUser()
      .subscribe(res => {
        this.user = res;
      });

    this._jobService.getJobQuestions(job_id)
      .subscribe(res => {

        // Sets a selected question if the service returns a list with content
        if (res.length) {
          this.setSelectedQuestion(res[0]);
        }
        this.questions = res;
      });

    this._jobService.getJob(job_id)
      .subscribe(res => {
        this.job = res;

        this.options.url = myGlobals.apiUrl + 'jobs/' + res.id + '/';

        this._companyService.getMessageTemplates(res.company.id)
          .subscribe(res => {
            this.templates = res;
          });
        this._jobService.getApplications(job_id)
          .subscribe(res => {
            if (res.length) {
              this.applications = res;

              if (application_id) {
                res.some(application => {
                  if (application.id == Number(application_id)) {
                    this.setSelectedApplication(application);
                    return true
                  }
                });
              } else {
                this.setSelectedApplication(res[0]);
              }
            }
            this.isLoading = false;
          });
      });
  }

  public setSelectedApplication(application) {
    this.selectedApplication = application;
    this._jobService.getApplicationNotes(application.id, application.job.id)
      .subscribe(res => {
        this.selectedApplication.notes = res;
      });
    this._userService.getUser(application.applicant.id)
      .subscribe(res => {
        this.selectedApplication.applicant = res;
      });
    this._userService.getApplicationAnswers(application.id)
      .subscribe(res => {
        if (res.length) {
          this.selectedApplication.answers = res;
        }
      });
  }

  public statusChange(newStatus) {

    // Checks for predefined template
    let ft = this.filterTemplates(newStatus);
    this.selectedTemplate = (ft.length) ? ft[0] : new MessageTemplate();

    this.selectedTemplate.status = newStatus;

    // Toggles the template view
    this.toggle('requiredMessage');
  }

  public changeStatus() {
    this.selectedApplication.status = this.selectedTemplate.status;

    this.sendMessage(false);

    this._jobService.updateApplication(this.selectedApplication)
      .subscribe(res => {
        this.toggle('requiredMessage');
        //Sorts the application list
        this.sortApplicants();
      });
  }

  public updateTemplate() {
    this._companyService.updateMessageTemplate(this.selectedTemplate)
      .subscribe(res => {
        this.alert();
      });
  }

  public updateJob() {
    this._jobService.updateJob(this.job)
      .subscribe(res => {
        this.toggle('dialog');
        this.alert();
      })
  }

  public updateApplicationInfo() {
    this._jobService.updateApplication(this.selectedApplication)
      .subscribe(res => {
        let ft = this.filterTemplates(res.status);
        this.selectedTemplate = (ft.length) ? ft[0] : new MessageTemplate();
        this.toggle('template');
        this.isUpdated = false;

        //Sorts the application list
        this.sortApplicants();
      });
  }


  /*
  * ================================
  *       QUESTION FUNCTIONS
  * ================================
  */

  public setSelectedQuestion(question) {
    this.selectedQuestion = question;
  }

  public checkSelectedQuestion(question) {
    return this.selectedQuestion == question;
  }

  public checkPress(event, alternative) {

    // Checks if the key code is equal of that of enter
    if (event.keyCode == 13) {

      alternative.question = this.selectedQuestion.id;

      this.selectedQuestion.alternatives.push(Object.assign({}, alternative));
      this.newAlternative = <QuestionAlternative>{};

      //Prevents line shift in text area
      event.preventDefault();
    }
  }

  public updateQuestion() {
    this._jobService.updateQuestion(this.selectedQuestion, this.job.id)
      .subscribe(res => {
        this.toggle('questions');
        this.alert();
      });
  }

  /*
   * ==================================
   *   FILTER DROPDOWN
   * ==================================
   */

  public setFilterValue(value) {
    this.filterValue = value;
  }

  public doChange() {
    this.isUpdated = true;
  }

  public postNote(text) {
    let note = {
      text: text,
      application: this.selectedApplication.id
    };

    this._jobService.postApplicationNote(note, this.selectedApplication.id, this.selectedApplication.job.id)
      .subscribe(res => {
        this.selectedApplication.notes.push(res);
        this.newNote = "";
      });
  }

  public toggle(key) {
    this.show[key] = !this.show[key];
  }

  public checkLength(list) {
    return list.length > 0;
  }

  public sendMessage(doToggle:boolean) {

    this._roomService.getRoom(this.selectedApplication.id)
      .subscribe(res => {
        // Initializes the new message
        let msg = new Message(this.selectedTemplate.text, res.id, localStorage.getItem('comp'), true);

        this._roomService.postMessage(msg)
          .subscribe(res => {
            this.showDialog();

            if (doToggle)
              this.toggle('template');
          });
      });
  }

  public checkStatus(status, expected) {
    return status === expected
  }

  // File Upload
  public handleDropUpload(data) {
    if (data && data.response) {
      this.job = JSON.parse(data.response);
      this.toggle('fileUpload');
      this.alert();
    }
  }

  // Alert
  public alert() {
    this.toggle('alert');
    setTimeout(() => {
      this.toggle('alert');
    }, 2000)
  }

  public covertDate(date) {
    return new Date(date);
  }

  public calcAge(dateString) {
    var birthday = +new Date(dateString);
    return ~~((Date.now() - birthday) / (31557600000));
  }

  public navigate(list) {
    this._router.navigate(list);
  }

  public setPicture(url: string) {
    if (url.indexOf('fbcdn') == -1)
      return this.baseUrl + url;

    return url;
  }

  // Private functions

  private filterTemplates(status) {
    let filterTemplates = [];
    for (var template of this.templates) {
      if (template.status == status) {
        filterTemplates.push(template);
      }
    }
    return filterTemplates;
  }

  private showDialog() {
    this.dialogText = "Message sent";
    this.toggle('mojobDialog');
  }

  private sortApplicants() {
    this.applications.sort((a:any, b:any) => {

      if (a.status < b.status) {
        return -1;
      } else if (a.status > b.status) {
        return 1;
      } else {
        return 0;
      }
    });
  }
}



