import {Component}                        from '@angular/core';
import {HTTP_PROVIDERS}                   from '@angular/http';
import {Router, RouteSegment}             from '@angular/router';

import {JobService}                       from "lib/services/jobService";
import {Job}                              from "lib/classes/job";
import myGlobals = require('globals');
import {Application} from "../../../lib/classes/application";
import {Question} from "../../../lib/classes/question";
import {Answer} from "../../../lib/classes/answer";
import {UserService} from "../../../lib/services/userService";
import {User} from "../../../lib/classes/user";

@Component({
  selector: 'job',
  template: require('pages/front/job/userjob.component.html'),
  directives: [],
  providers: [
    HTTP_PROVIDERS,
    JobService
  ],
  styles: [require('css/userjob.component.css'), require('css/app.component.css')]
})

export class JobComponent {

  /*
   *  ==================================
   *    PUBLIC FIELDS
   *  ==================================
   */

  // Related to logged in user
  public user: User;

  // Related to the job
  public job: Job = <Job>{};
  public questions: Question[] = [];
  public coverLetter: string;
  public answers: {} = {};
  public application: Application = <Application>{};
  public hasApplied: boolean;

  // Other
  public baseUrl: string;
  public show:{};
  public step:number;

  constructor(
    private _router:Router,
    private _jobService:JobService,
    private _userService:UserService,
    private _routeSegment:RouteSegment) {

    // Baseurl to get static-files from server
    this.baseUrl = myGlobals.baseURL;

    // Retrieves the ID from the route parameters
    var jobId = this._routeSegment.getParam('id');

    // Initializes the job object from server
    this._jobService.getJob(jobId)
      .subscribe(job => {
        this.job = job;
      });

    this._jobService.getJobQuestions(jobId)
      .subscribe(questions => {

        this.questions = questions;
      });

    this._jobService.checkApplication(jobId)
      .subscribe(res => {
        this.hasApplied = res;
      });

    // Show/hide properties
    this.show = {
      dialog:false
    }

    this.step = 0;

    // Retrieves the logged in user
    this._userService.getCurrentUser()
      .subscribe(user => {
        this.user = user;
      });
  }

  /*
   *  ==================================
   *    PUBLIC FUNCTIONS
   *  ==================================
   */

  // Show/hide related
  public toggle(key) {
    this.show[key] = !this.show[key];
  }

  public checkStep(step) {
    return this.step == step;
  }

  public checkFinish() {
    return this.step >= this.questions.length;
  }

  public next() {

    if (this.step > 0 && !this.questions[this.step-1].alternatives.length) {
      var question = this.questions[this.step-1];
      this.saveAnswer(question, question.answer, 1);
    }

    this.step += 1;
  }

  public prev() {
    this.step -= 1;
  }

  // Job related
  public saveAnswer(question, answer, type) {

    if (type) {
      this.answers[question.id] = new Answer(question.id, answer, null, type);
    } else {
      this.answers[question.id] = new Answer(question.id, null, answer, type);
    }
  }

  public postApplication() {
    var answerList = [];

    for (var key in this.answers) {
      answerList.push(this.answers[key]);
    }

    var application = new Application(this.job.id, this.user.id, this.coverLetter, answerList);

    this._jobService.postApplication(application)
      .subscribe(application => {
        console.log(application);
      });
  }

  public navigate(path) {
    this._router.navigate(['front', path]);
  }
}



