import {Component, OnInit} from 'angular2/core';
import {HTTP_PROVIDERS}    from 'angular2/http';
import {CanActivate} from "angular2/router";
import {CompanyService} from "../../../lib/services/companyService";
import {Company} from "../../../lib/classes/company";
import {NgZone} from "angular2/core";
import {UPLOAD_DIRECTIVES} from 'ng2-uploader/ng2-uploader';
import {PopupAlert} from "../../../components/popupalert/popupalert.component";
import {ComponentInstruction} from "angular2/router";
import {isLoggedIn} from "../../../lib/const/authCheck";
import myGlobals = require('globals');

@Component({
    selector: 'company',
    template: require('pages/dash/company/company.component.html'),
    directives: [UPLOAD_DIRECTIVES, PopupAlert],
    providers: [
        HTTP_PROVIDERS,
        CompanyService
    ],
    styles: [require('css/company.component.css'), require('css/app.component.css')]
})

@CanActivate((next: ComponentInstruction, previous: ComponentInstruction) => {
  return isLoggedIn(next, previous);
})
export class CompanyComponent implements OnInit {

    public company: Company = <Company>{};
    public show: {};
    public isLoading: boolean;
    public baseUrl: string;

    // File upload
    zone: NgZone;
    options: Object = {
        url: myGlobals.apiUrl + 'companies/' + localStorage.getItem('comp') + '/',
        fieldName: 'logo',
        method: 'PUT',
        withCredentials: true,
        authToken: localStorage.getItem('id_token'),
        authTokenPrefix: "JWT"
    };
    dropzone: any;
    imageProgress: number = 0;
    imageResp: any[] = [];

    constructor(private _companyService: CompanyService){
        this.show = {
            'dialog':false
        };

        this.zone = new NgZone({ enableLongStackTrace: false});
        this.isLoading = false;
        this.baseUrl = myGlobals.baseURL;
    }

    public ngOnInit () {
        let company_id = localStorage.getItem('comp');
        this._companyService.getCompany(company_id)
            .subscribe(res => {
                this.company = res;
            });
    }

    public toggle(key) {
        this.show[key] = !this.show[key];
    }

    public updateCompany () {
        this._companyService.update(this.company)
            .subscribe(res => {
                this.alert();
            });
    }

    // Alert
    public alert() {
        this.toggle('alert');
        setTimeout(() => {
            this.toggle('alert');
        }, 2000)
    }

    // File Upload
    public handleDropUpload(data) {
        if (data && data.response) {
            this.isLoading = false;
            this.company = JSON.parse(data.response);
            this.toggle('dialog');
        }
    }

    public uploadStart($event) {
        this.isLoading = true;
    }
}



