import {Component, OnInit} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {CanActivate, ComponentInstruction} from "@angular/router-deprecated";
import {ROUTER_DIRECTIVES} from "@angular/router";

import {JobService} from "../../../lib/services/jobService";
import {Job} from "../../../lib/classes/job";
import {isLoggedIn} from "../../../lib/const/authCheck";
import {CompanyService} from "../../../lib/services/companyService";
import {Room} from "../../../lib/classes/room";
import myGlobals = require('globals');

@Component({
  selector: 'dashboard',
  template: require('pages/dash/dashboard/dashboard.component.html'),
  directives: [ROUTER_DIRECTIVES],
  providers: [
    HTTP_PROVIDERS
  ],
  styles: [require('css/dashboard.component.css'), require('css/app.component.css')]
})

@CanActivate((next:ComponentInstruction, previous:ComponentInstruction) => {
  return isLoggedIn(next, previous);
})

export class DashboardComponent implements OnInit {
  public jobs:Job[];
  public rooms:Room[];
  public dashboardInfo:{} = {};

  public isLoading:boolean;
  public baseUrl:string;

  constructor(private _jobService:JobService,
              private _companyServie:CompanyService) {
    this.rooms = [];
    this.baseUrl = myGlobals.baseURL;
  }

  public ngOnInit() {
    var company_id = localStorage.getItem('comp');
    this.isLoading = true;
    this._jobService.getCompanyJobs(company_id)
      .subscribe(res => {
        this.jobs = res;

        this.isLoading = false;

        // Retrieves the total number of applicants
        var applicants = 0;
        res.forEach(job => {
          applicants += job.numb_applicants
        });

        // Gathers some information about the company
        this.dashboardInfo = {
          jobs: res.length,
          applicants: applicants
        }
      });

    this._companyServie.getRooms(localStorage.getItem('comp'))
      .subscribe(res => {
        this.rooms = res;
      });
  }
}



