import {Component, OnInit} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';

import {CompanyService} from "../../../lib/services/companyService";
import {PopupAlert} from "../../../components/popupalert/popupalert.component";
import {MessageTemplate} from "../../../lib/classes/messageTemplate";
import {ContentService} from "../../../lib/services/contentService";
import {SearchPipe} from "../../../pipes/search.pipe";

@Component({
    selector: 'templates',
    template: require('pages/dash/templates/templates.component.html'),
    directives: [PopupAlert],
    pipes: [SearchPipe],
    providers: [
        HTTP_PROVIDERS,
        CompanyService
    ],
    styles: [require('css/templates.component.css'), require('css/app.component.css')]
})

export class TemplateComponent implements OnInit {
    public show:{};
    public searchTerm:string;
    public templates:MessageTemplate[] = [];
    public selectedTemplate:MessageTemplate = <MessageTemplate>{};
    public cs:ContentService;

    public templateErrors:string[] = [];

    constructor(private _companyService:CompanyService) {
        this.show = {
            'dialog': false,
            'newdialog': false
        };
        this.searchTerm = '';
        this.cs = new ContentService();
    }

    public ngOnInit() {
        this._companyService.getMessageTemplates(localStorage.getItem('comp'))
            .subscribe(res => {
                this.templates = res;
            });
    }

    public setNewTemplate() {
        this.selectedTemplate = <MessageTemplate>{};
        this.toggle('newdialog');
    }

    public createTemplate() {
        let template = this.selectedTemplate;
        template.company = localStorage.getItem('comp');

        var exists = false;

        // Checks if template already exists
        this.templates.forEach(temp => {
            if (temp.status == template.status) {
                exists = true;
                this.templateErrors.push('Du har allerede en meldingsmal for denne statusen');
                return;
            }
        });

        if (!exists) {
            this._companyService.createMessageTemplate(template)
                .subscribe(res => {
                    this.templates.push(res);
                    this.searchTerm = '';
                    this.toggle('newdialog');
                });
        }
    }

    public updateTemplate() {
        this._companyService.updateMessageTemplate(this.selectedTemplate)
            .subscribe(res => {
                this.toggle('dialog');
                this.alert();
            })
    }

    public setSelectedTemplate(template:MessageTemplate) {
        this.selectedTemplate = template;
        this.toggle('dialog');
    }

    public toggle(key) {
        this.show[key] = !this.show[key];
    }

    // Alert
    public alert() {
        this.toggle('alert');
        setTimeout(() => {
            this.toggle('alert');
        }, 2000)
    }
}



