import {Component, OnInit} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {Router, ROUTER_DIRECTIVES} from "@angular/router";
import {CanActivate} from "@angular/router-deprecated";

import {Job} from "../../../lib/classes/job";
import {SearchPipe} from "../../../pipes/search.pipe";
import {JobService} from "../../../lib/services/jobService";
import {ComponentInstruction} from "angular2/router";
import {isLoggedIn} from "../../../lib/const/authCheck";

@Component({
    selector: 'myjobs',
    template: require('pages/dash/myjobs/myjobs.component.html'),
    pipes: [SearchPipe],
    directives: [ROUTER_DIRECTIVES],
    providers: [
        HTTP_PROVIDERS
    ],
    styles: [require('css/myjobs.component.css'), require('css/app.component.css')]
})

@CanActivate((next: ComponentInstruction, previous: ComponentInstruction) => {
  return isLoggedIn(next, previous);
})

export class MyJobsComponent implements OnInit {

    // Global fields
    public jobs: Job[] = [];
    public searchTerm: string;
    public isLoading: boolean;

    constructor(private _router:Router, private _jobService: JobService) {
        this.searchTerm = '';
    };

    ngOnInit() {
        let company_id = localStorage.getItem('comp');
        this.isLoading = true;
        this._jobService.getCompanyJobs(company_id)
            .subscribe(res => {
                this.jobs = res;
                this.isLoading = false;
            });
    }

    navigate(path, job_id) {
        this._router.navigate(['dashboard', path, job_id]);
    }

    public covertDate(date) {
        return new Date(date);
    }
}



