import {Component, Input, Output, EventEmitter, ElementRef} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {mapToIterablePipe} from "../../pipes/mapToIterable";


@Component({
  selector: 'dropdown-filter',
  template: require('components/dropdownfilter/dropdownfilter.component.html'),
  directives: [],
  providers: [
    HTTP_PROVIDERS
  ],
  styles: [require('css/dropdownfilter.component.css'), require('css/app.component.css')],
  pipes: [mapToIterablePipe],
  host: {
    '(document:click)': 'onClick($event)',
  },
})

export class DropdownFilter  {
  @Input() fields:{} = {};
  @Output() onFilter = new EventEmitter<number>();

  // Internal fields
  public filterValue:any = 'default';
  public showFilter: boolean;
  public elementRef;
  public filterObject:{};

  constructor(element: ElementRef) {
    this.showFilter = false;
    this.elementRef = element;

    this.setFilterObject();
  }

  public toggleList() {
    this.showFilter = !this.showFilter;
  }

  public setFilterValue(value) {
    this.onFilter.emit(value);
    this.filterValue = value;

    this.setFilterObject();
    this.toggleList();
  }

  public setFilterObject() {
    var id = this.filterValue;
    this.filterObject = (id == 'default') ? {text: 'Alle', icon: 'fa-users'} : this.fields[id];
  }

  public onClick(event) {
    var clickedComponent = event.target;

    if (!this.elementRef.nativeElement.contains(clickedComponent)) // or some similar check
      this.showFilter = false;
  }
}



