import {Component, NgZone} from '@angular/core';
import {HTTP_PROVIDERS}    from '@angular/http';
import {FormBuilder, Validators} from "@angular/common";
import {Router, ROUTER_DIRECTIVES} from "@angular/router";
import {ComponentInstruction, CanActivate} from "@angular/router-deprecated";

import {Datepicker} from "../../../components/datepicker/datepicker";
import {JobService} from "../../../lib/services/jobService";
import {isLoggedIn} from "../../../lib/const/authCheck";
import {Job} from "../../../lib/classes/job";
import {Question} from "../../../lib/classes/question";
import {QuestionAlternative} from "../../../lib/classes/questionAlternative";
import {UPLOAD_DIRECTIVES} from 'ng2-uploader/ng2-uploader';

import myGlobals = require('globals');
import {GoogleplaceDirective} from "lib/directives/googlePlace";
import {CompanyService} from "../../../lib/services/companyService";
import {Company} from "../../../lib/classes/company";
import {ContentService} from "../../../lib/services/contentService";
import {mapToIterablePipe} from "../../../pipes/mapToIterable";
import {Observable} from "rxjs/Rx";

@Component({
  selector: 'post',
  template: require('pages/dash/post/post.component.html'),
  directives: [Datepicker, UPLOAD_DIRECTIVES, ROUTER_DIRECTIVES, GoogleplaceDirective],
  providers: [
    HTTP_PROVIDERS,
    CompanyService
  ],
  styles: [require('css/post.component.css'), require('css/app.component.css')],
  pipes: [mapToIterablePipe]
})

@CanActivate((next:ComponentInstruction, previous:ComponentInstruction) => {
  return isLoggedIn(next, previous);
})

export class PostComponent {

  // Job related
  public newJob:Job = <Job>{};
  public newQuestion:Question = <Question>{};
  public newAlternative:QuestionAlternative = <QuestionAlternative>{};
  public soknadsfrist = "";
  public userForm:any;
  public errors:string[];
  public isLoading:boolean;
  public company:Company;
  public cs:ContentService;

  public isMultipleChoice:boolean;
  public visible:number;
  public baseUrl:string;

  // Category related
  public categoryImages:any[];

  public show:{};

  // File upload
  zone:NgZone;
  options = {
    url: myGlobals.apiUrl + 'jobs/',
    fieldName: 'image',
    method: 'PUT',
    withCredentials: true,
    authToken: localStorage.getItem('id_token'),
    authTokenPrefix: "JWT"
  };

  dropzone:any;
  imageProgress:number = 0;
  imageResp:any[] = [];

  constructor(private _jobService:JobService,
              private _companyService:CompanyService,
              private _router:Router,
              private _formBuilder:FormBuilder) {
    this.userForm = this._formBuilder.group({
      'title': ['', Validators.required],
      'description': ['', Validators.required],
      'address': ['', Validators.required],
      'salary': ['', Validators.required]
    });

    this.newJob.questions = [];
    this.newJob.salary = '';
    this.newQuestion.alternatives = [];
    this.errors = [];
    this.isMultipleChoice = true;
    this.isLoading = false;
    this.visible = 1;
    this.baseUrl = myGlobals.baseURL;

    this.show = {
      dialog: false,
      fileUpload: true,
      summary: false,
      editJob: false,
      pictureLib: false
    };

    this.cs = new ContentService();

    this._companyService.getCompany(localStorage.getItem('comp'))
      .subscribe(company => {
        this.company = company;
      });

    // File upload
    this.zone = new NgZone({enableLongStackTrace: false});
  }

  public postJob() {
    this.errors = [];

    if (!this.newQuestion.question || !this.newQuestion.question.length) {
      this.isLoading = true;
      this._jobService.postJob(this.newJob).subscribe(res => {
        this.isLoading = false;
        this.newJob = res;

        this.options.url = myGlobals.apiUrl + 'jobs/' + res.id + '/';

        // Retrieves the stock photos related to the selected category
        this.getCategoryImages(res.category);

        this.toggle('dialog');
      });
    } else {
      this.errors.push('Du prøver å poste jobben, men du har påbegynt et spørsmål. Visk det ut, eller legg det til.');
    }
  }

  public updateJob(doRoute:boolean) {
    this.isLoading = true;
    this._jobService.updateJob(this.newJob)
      .subscribe(res => {
        this.isLoading = false;

        if (doRoute)
          this._router.navigate(['dashboard', 'myjobs']);
        else
          this.newJob = res;
      });
  }

  public publishJob() {
    this.newJob.is_published = true;
    this.updateJob(true);
  }

  public setCategory(cat:number) {
    this.newJob.category = cat;
    this.next();
  }

  public dateChanged(event) {
    var d = event.value;
    d.setHours(23, 59, 59);
    d.setTime(d.getTime() - d.getTimezoneOffset() * 60 * 1000);

    this.newJob.due_date = d.toISOString();
    this.next();
  }

  public addQuestion(question) {

    this.errors = [];

    if (question.question && question.question.length > 0) {

      if (this.newAlternative.text) {
        this.newQuestion.alternatives.push(Object.assign({}, this.newAlternative));
      }

      if (this.isMultipleChoice) {

        if (this.newQuestion.alternatives.length < 2) {
          if (this.newAlternative.text)
            this.newQuestion.alternatives.pop();

          this.errors.push('Minimum to alternativer er påkrevd')

          return
        }

        var at_least_one = this.newQuestion.alternatives.some(alt => {
          return alt.is_correct;
        });

        if (!at_least_one) {
          if (this.newAlternative.text)
            this.newQuestion.alternatives.pop();

          this.errors.push('Minimum ett av svaralternativene må merkes som riktig');

          return
        }

      }

      this.newJob.questions.push(Object.assign({}, question))
      this.newQuestion = <Question>{};
      this.newQuestion.alternatives = [];

      this.newAlternative = <QuestionAlternative>{};

      return;
    }

    this.errors.push('Vennligst skriv inn et spørsmål')
  }

  public deleteQuestion(index) {
    this.newJob.questions.splice(index, 1)
  }

  public checkPress(event, alternative) {

    // Checks if the key code is equal of that of enter
    if (event.keyCode == 13) {

      this.newQuestion.alternatives.push(Object.assign({}, alternative));
      this.newAlternative = <QuestionAlternative>{};

      //Prevents line shift in text area
      event.preventDefault();
    }
  }

  // Google Place
  public getAddress(place:Object) {
    this.newJob.address = place['formatted_address'];
  }

  // File Upload
  public handleDropUpload(data) {
    if (data && data.response) {
      this.newJob = JSON.parse(data.response);
      this.isLoading = false;
      this.show['summary'] = true;
      this.toggle('fileUpload');
      setTimeout(() => {
        this.toggle('fileUpload');
      }, 200);
    }
  }

  /*
   * =========================================
   *   Category Images
   * =========================================
   */

  public getCategoryImages(id) {
    this._jobService.getCategoryImages(id)
      .subscribe(images => {
        this.categoryImages = images;
      });
  }

  public setCategoryImage(id) {
    this.newJob.stock_photo = id;
    this.show['summary'] = true;
    this.updateJob(false);
  }

  public uploadStart($event) {
    this.isLoading = true;
  }

  public setActive(bool) {
    this.isMultipleChoice = bool;
  }

  public next() {
    this.visible += 1;
  }

  public prev() {
    this.visible -= 1;
  }

  public toggle(key) {
    this.show[key] = !this.show[key];
  }

  public checkVisible(numb) {
    return this.visible == numb;
  }
}



