export class Answer {
  public question: number;
  public application: number;
  public text_answer: string;
  public radio_answer: number;
  public type: number;

  constructor(
    question: number,
    text_answer: string,
    radio_answer: number,
    type: number) {

    this.question = question;
    this.text_answer = text_answer;
    this.radio_answer = radio_answer;
    this.type = type;
  }
}



