import 'rxjs/Rx';
import {Injectable}     from '@angular/core';


@Injectable()
export class ContentService {

  private statuses:{};
  private salaries:{};
  private categories:{};

  constructor() {
    this.statuses = {
      0: {text: 'Ny søker', icon: 'fa-question', status: 0},
      1: {text: 'Ansatt', icon: 'fa-briefcase', status: 1},
      2: {text: 'Tilbud', icon: 'fa-file-text-o', status: 2},
      3: {text: 'Intervju', icon: 'fa-building', status: 3},
      4: {text: 'Kvalifisert', icon: 'fa-check', status: 4},
      5: {text: 'Avslått', icon: 'fa-times', status: 5}
    };

    this.salaries = {
      0: 'Ikke oppgitt',
      1: '100-125 kr/t',
      2: '125-150 kr/t',
      3: '150-175 kr/t',
      4: '175-200 kr/t',
      5: '200-225 kr/t',
      6: '225-250 kr/t',
    }


    /*
    * ============================================
    *     CATEGORIES - As defined on server
    * ============================================
    */

    /*
    this.categories = {
      0: {name: 'Butikk', logo: 'store', value: 0},
      1: {name: 'Restaurant & Hotel', logo: 'hotel', value: 1},
      2: {name: 'Omsorg', logo: 'care', value: 2},
      3: {name: 'Barn & Utdannelse', logo: 'teaching', value: 3},
      4: {name: 'It & Design', logo: 'it', value: 4},
      5: {name: 'Salg & Markedsføring', logo: 'sales', value: 5},
      6: {name: 'Kino & Opplevelser', logo: 'cinema', value: 6},
      7: {name: 'Manuelt arbeid', logo: 'labour', value: 7},
      8: {name: 'Modell', logo: 'model', value: 8},
      9: {name: 'Internships', logo: 'internship', value: 9},
      10: {name: 'Frivillig arbeid', logo: 'voulenteer', value: 10},
      11: {name: 'Annet', logo: 'other', value: 11}
    };
    */

    this.categories = {
      0: {name: 'Dagligvare', logo: 'store', value: 0},
      1: {name: 'Restaurant', logo: 'sales', value: 1},
      2: {name: 'Omsorg', logo: 'care', value: 2},
      3: {name: 'Butikk', logo: 'labour', value: 3},
      4: {name: 'Hotell', logo: 'hotel', value: 4},
      5: {name: 'Salg', logo: 'internship', value: 5}
    };
  }

  public getStatus(key) {
    return this.statuses[key].text;
  }

  public getStatusIcon(key) {
    return this.statuses[key].icon;
  }

  public getSalary(key) {
    return this.salaries[key];
  }

  public calcNumbDays(date) {
    var day = 24*60*60*1000; // hours*minutes*seconds*milliseconds
    var now = new Date();
    var due_date = new Date(date);
    return Math.round(Math.abs((now.getTime() - due_date.getTime())/(day)));
  }

  /*
   * ============================================
   *     CATEGORIES
   * ============================================
   */

  public getCat(key) {
    return this.categories[key];
  }
}



