import 'rxjs/Rx';
import {Injectable}     from 'angular2/core';
import {AuthHttp} from 'angular2-jwt/angular2-jwt';
import {Message} from "../classes/message";
import {Room} from "../classes/room";
import myGlobals = require('globals');
import {RequestService} from "./requestService";

@Injectable()
export class RoomService {

  private _apiEndpoint = myGlobals.apiUrl + 'chats/';  // URL to web api

  public doUpdate:boolean;

  constructor(private http:AuthHttp,
              private _requestService:RequestService) {
    this.doUpdate = true;
  }

  public getMessages(roomId) {
    return this._requestService.request('GET', this._apiEndpoint + 'room/' + roomId + '/')
      .do(() => {
        this.doUpdate = false;
      })
      .map(res => <Message[]> res.json());
  }

  public postMessage(msg) {
    return this._requestService.request('POST', this._apiEndpoint + 'room/' + msg.room + '/', '', msg)
      .map(res => <Message> res.json());
  }

  public sendBulkMessage(msg, idList) {
    let body = {'message': msg, 'id_list': idList};
    return this._requestService.request('POST', this._apiEndpoint + 'room/bulk_message/' + msg.company + '/', '', body);
  }

  public getRoom(application_id) {
    return this._requestService.request('GET', this._apiEndpoint + 'application-room/' + application_id + '/')
      .map(res => <Room> res.json());
  }
}



